@font-face {
    font-family: 'Love';
    src: url('./fonts/FiraSans/FiraSans-Regular.ttf'), format('truetype');
}

body {   
    margin: 0px;
    font-family: 'Love', sans-serif;
    background-color: #a2d2ff;
}

#page-container {
    margin: 0px;
    padding: 0px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.calculator {
    display: grid;
    grid-template-columns: repeat(4, 75px);
}

.display {
    font-family: 'Love';
    border-radius: 15px;
    grid-column-start: 1;
    grid-column-end: 5;
    height: 80px;
    border-top: 3px solid #e5e5e5;
    text-align: right;
    font-size: 55px;
    line-height: 80px;
    padding-right: 10px;
    margin: 0px 5px 15px 5px;
    background-color: #000000;
    color: white;
}

button {
    margin: 3px;
    font-size: 25px;
    height: 60px;
    border: none;
    background-color: #343a40;
    color: white;
    border-radius: 30px;
}

button:active {
    background-color: rgba(255,255,255,70%);
}

.equals {
    background-color: #fca311;
}

.main-btn {
    background-color: #6c757d;
}

.btn-operator {
    background-color: #fca311;
    border: none;
    font-weight: bolder;
}

.heart-btn {
    padding-top: 8px;
    background-color: black;
    border: 1px solid gray;
}

.backspace-btn {
    padding-top: 6px;
    padding-right: 10px;
}

.heart-btn:hover {
    cursor: pointer;
}

.foot {
    grid-column-start: 1;
    grid-column-end: 5;
    border-radius: 35px;
    height: 18px;
    border-bottom: 4px solid white;
    text-align: center;
    line-height: 38px;
    margin-top: 10px;
}

#root {
    border: 1px solid lightgray;
    border-radius: 19px;
    display: inline-block;
    padding: 15px;
    background-color: #000000;
}

.oneTo3 {
    grid-column-start: 1;
    grid-column-end: 3;
}
